<template>
  <div>
    <input 
      type="text" 
      class="ds-input" 
      :placeholder="placeholder"
      v-model="selectedDataRowKeyName"
      @change="setDataRowKeyName" />
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number,
    dataRow: Object
  },
  setup(props) {
    const store = useStore();
    const placeholder = "Data Key Name 입력";
    let selectedDataRowKeyName = props.dataRow ? computed(() => props.dataRow.item.itemKeyName) : ref("");

    // 선택한 data keyname을 store에 저장
    const setDataRowKeyName = (() => {
      store.commit("setDataRowKeyName", {
        index: props.index,
        dataRowKeyName: selectedDataRowKeyName.value
      });
      console.log("dataRowKeyName -->", store.getters.getDataRowKeyName(props.index));
    });

    return {
      placeholder,
      selectedDataRowKeyName,
      setDataRowKeyName
    };
  },
});
</script>

<style lang="scss" scoped></style>
